* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  background-image: linear-gradient(120deg, #f6d365 0%, #fda085 100%);
  color: white;
  font-family: "Poppins", sans-serif;
}

header {
  text-align: center;
}
form {
  display: flex;
  justify-content: center;
}
form button {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 25px;
  height: 25px;
  transition: all .5s;
}

form button:hover {
  background-color: #ff6f47;
}

form button i {
  color:#ff6f47;
  transition: all .5s;
}

form button:hover i {
  color:#fff;
}

.todo {
  display: flex;
  justify-content: center;
  align-items: center;
  list-style-type: none;
  padding:10px;
  font-size: 30px;
}

.completed {
  text-decoration: line-through;
  opacity: 0.5;
}

